<template>
  <!-- hhh  -->
    <div>
        <div class="order">
            <ul class="order-list" v-for="(category, cate) in categories" :key="category.id"><b>{{ cate }}<i class="el-icon-caret-bottom"></i></b>
                <li v-for="item in category" :key="item.id" v-bind:class="{isred: item.isRed}" @click="toggle(item)"><span>{{ item.name }}</span> &nbsp; {{ item.price }}元</li>
            </ul>
        </div>

        <div class="tip">
            <!-- 订单信息 -->
            <ul class="order-list-pay">
                <li v-for="item in newItems" :key="item.id" @click="deleteItem(item)">{{ item.name }}&nbsp; {{ item.price }}元</li>
            </ul>
            <!-- 底栏 -->
            <div class="total" id="total" v-show="ishow">
                <el-input type="text" :maxlength="2" placeholder="人数" v-model="num" style="width:30%; margin-right: 3%;"></el-input>
                <el-time-picker v-model="time" :picker-options="{ selectableRange: '11:00:00 - 13:00:00' }" placeholder="到店时间" style="width:67%;"></el-time-picker>
                <div style="height:10px; width:100%;"></div>
                <el-input type="textarea" :rows="2" placeholder="备注信息" v-model="remark"></el-input>
                <div style="height:10px; width:100%;"></div>
                <el-button type="normal" size="small" @click="dialogVisible = true, changeTime()">生成订单</el-button>
                <span>totalPrice:{{ totalprice }}元</span>
            </div>
            <!-- 提交信息确认 -->
            <el-dialog title="订单确认" :visible.sync="dialogVisible" width="30%">
                <p>订单：{{ content }}</p>
                <p>人数：{{ num }}</p>
                <p>到店时间：{{ strTime }}</p>
                <p>备注：{{ remark }}</p>
                <span>总计：{{ totalprice }}元</span>
                <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false, sendOrder()">发 送</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
                </span>
            </el-dialog>
        </div>
        <!-- 建议和意见 -->
        <footer>
            <el-input type="textarea" v-model="suggestion" placeholder="意见和建议" style="width:90%"></el-input>
            <el-button style="width:8%;" @click="sendSuggestion()" :plain="true">发 送</el-button>
        </footer>
    </div>

</template>

<script>
export default {
  name: "meal",
  data() {
    return {
      cates: [],
      categories: [
        // {
        //   name: "手撕包菜",
        //   price: 11,
        //   isRed: false
        // },
        // {
        //   name: "干锅肥肠",
        //   price: 25,
        //   isRed: false
        // },
      ],
      // 备注
      remark: null,
      // 已点的菜的item
      newItems: [],
      // 总价
      totalprice: 0,
      // 是否显示订单
      ishow: false,
      dialogVisible: false,
      // 已点的菜
      content: "",
      num: null,
      time: new Date("Mon Oct 29 2018 12:05:00 GMT+0800 (中国标准时间)"),
      strTime: "12:05",
      suggestion: ""
    };
  },

  created: function() {
    // `this` 指向 vm 实例
    const url = "http://47.100.226.195:8000/api/content";
    // const url = "http://localhost:8010/api/content";
    this.$axios({
      method: "GET",
      url: url
      // data: params
    }).then(res => {
      this.categories = res["data"]["content"]["r"];
      for (var key in this.categories) {
        this.cates.push(key);
      }
    });

    // var month = date.getMonth() + 1
    // var day = date.getDate()
    // var hour  = date.getHours()
    // var minute = date.getMinutes()
    // this.time = month + "月" + day + " " + hour + ":" + minute
  },

  methods: {
    sendOrder: function() {
      const url = "http://47.100.226.195:8000/api/order";
      // const url = "http://localhost:8010/api/order";
      var params = new URLSearchParams();
      params.append("content", this.content);
      params.append("num", this.num);
      params.append("strTime", this.strTime);
      params.append("remark", this.remark);
      params.append("totalprice", this.totalprice);
      this.$axios({
        method: "POST",
        url: url,
        data: params
      }).then(res => {
        console.log(res["data"]);
        if (res["data"]["code"] == 200) {
          this.$message({
            message: "发送成功",
            type: "success"
          });
        } else if (res["data"]["code"] == 200) {
          this.$message({
            message: "发送失败",
            type: "error"
          });
        }
      });
    },

    toggle: function(item) {
      item.isRed = !item.isRed;
      if (item.isRed) {
        this.newItems.push({
          name: item.name,
          price: item.price
        });
      } else {
        this.newItems = this.newItems.filter(i => {
          return i.name != item.name;
        });
      }
      var totalprice = 0;
      for (var i = 0; i < this.newItems.length; i++) {
        totalprice += this.newItems[i].price;
      }
      this.totalprice = totalprice;
      if (this.totalprice == 0) {
        this.ishow = false;
      } else {
        this.ishow = true;
      }
      this.createOrder();
    },

    createOrder: function() {
      // 获取订单菜品信息
      var content = "";
      for (var i = 0; i < this.newItems.length; i++) {
        content += this.newItems[i].name + "、";
      }
      content = content.substring(0, content.length - 1);
      this.content = content;
    },

    deleteItem: function(item) {
      this.totalprice -= item.price;
      this.newItems = this.newItems.filter(i => {
        return i.name != item.name;
      });

      if (this.totalprice == 0) {
        this.ishow = false;
      }

      for (var cates in this.categories) {
        for (var idx in this.categories[cates]) {
          if (this.categories[cates][idx].name == item.name) {
            this.categories[cates][idx].isRed = !this.categories[cates][idx]
              .isRed;
            return;
          }
        }
      }
    },

    changeTime: function() {
      if (this.time == "") {
        this.strTime = "12:00";
      } else {
        var hour = String(this.time.getHours());
        var minute = String(this.time.getMinutes());
        if (minute.length == 1) {
          minute = "0" + minute;
        }
        this.strTime = hour + ":" + minute;
      }
      console.log(this.strTime);
    },

    sendSuggestion: function() {
      let suggestion = this.suggestion;
      if (suggestion.length < 5) {
        this.$message({
          message: "意见过短",
          type: "warning"
        });
      } else {
        const url = "http://47.100.226.195:8000/api/suggestion";
        // const url = "http://localhost:8010/api/suggestion";

        var params = new URLSearchParams();
        params.append("suggestion", suggestion);
        console.log(suggestion)
        this.$axios({
          method: "POST",
          url: url,
          data: params
        }).then(res => {
          console.log(res["data"]);
          if (res["data"]["code"] == 200) {
            this.$message({
              message: "发送成功",
              type: "success"
            });
            this.suggestion = ''
          } else if (res["data"]["code"] == 200) {
            this.$message({
              message: "发送失败",
              type: "error"
            });
          }
        });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.isred {
  color: red;
}

.order {
  width: 70%;
  display: flex;
  text-align: left;
  flex-direction: row;
  float: left;
}

.order-list {
  width: 16%;
  list-style: none;
  text-align: left;
  line-height: 30px;
  padding: 0px;
}

.order-list span {
  text-decoration: underline;
}

.tip {
  width: 30%;
  /* height: calc(100% - 54px); */
  /* overflow-y: hidden; */
  float: left;
  text-align: left;
  margin-bottom: 54px;
}

.order-list-pay {
  list-style: decimal;
  text-align: left;
  line-height: 30px;
  padding: 0px;
}

.total {
  display: none;
  height: 30px;
  /* line-height: 40px; */
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  /* justify-content: space-around; */
}

.total span {
  margin-left: 3%;
  border-radius: 3px;
  padding-left: 14px;
  padding-right: 14px;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  border: 1px solid #dddddd;
}

footer {
  width: 100%;
  position: fixed;
  right: 0;
  left: 0;
  bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
</style>



